<template>
  <div>
    <el-form
      :model="loginForm"
      :rules="loginRules"
      ref="loginFormRef"
      label-width="100%"
    >
      <el-form-item prop="email">
        <el-input
          v-model.trim="loginForm.email"
          prefix-icon="el-icon-message"
          placeholder=""
        >
        </el-input>
      </el-form-item>
      <el-form-item prop="password">
        <el-input
          v-model.trim="loginForm.password"
          prefix-icon="el-icon-lock"
          placeholder=""
          type="password"
        ></el-input>
      </el-form-item>
      <el-form-item prop="repassword" v-if="!isLogin">
        <el-input
          v-model.trim="loginForm.repassword"
          prefix-icon="el-icon-lock"
          placeholder=""
          type="password"
        ></el-input>
      </el-form-item>
      <el-form-item prop="code" v-if="!isLogin">
        <el-input
          v-model.trim="loginForm.code"
          prefix-icon="el-icon-key"
          placeholder=""
        >
          <security-code :email="loginForm.email" slot="append"></security-code>
        </el-input>
      </el-form-item>
      <el-form-item>
        <el-button
          class="submitBtn"
          v-if="submitBtnLoading"
          icon="el-icon-loading"
          >{{ isLogin ? '...' : '...' }}</el-button
        >
        <el-button @click="onSubmit" class="submitBtn" v-else>{{
          isLogin ? '' : ''
        }}</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import { login, register } from '@/api/user.js'
import SecurityCode from '@/components/security-code'
export default {
  name: 'LoginReginsterForm',
  components: {
    SecurityCode
  },
  model: { prop: 'isLogin', event: 'update-is_Login' },
  props: {
    isLogin: {
      type: Boolean,
      require: true
    }
  },
  data () {
    var vaildateRePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
      } else {
        if (value !== this.loginForm.password) {
          callback(new Error(''))
        } else {
          callback()
        }
      }
    }
    var vaildateEmail = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
      } else {
        const phoneReg =
          /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/
        if (phoneReg.test(value)) {
          callback()
        } else {
          callback(new Error(''))
        }
      }
    }
    var validateCode = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
      } else {
        if (!Number.isInteger(+value)) {
          callback(new Error(''))
        } else {
          if (value.length === 4) {
            callback()
          } else {
            callback(new Error(''))
          }
        }
      }
    }
    return {
      loginForm: {
        email: '',
        password: ''
      },
      loginRules: {
        email: [{ validator: vaildateEmail, trigger: 'blur' }],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 12, message: ' 3  12 ', trigger: 'blur' }
        ],
        repassword: [{ validator: vaildateRePass, trigger: 'blur' }],
        code: [{ validator: validateCode, trigger: 'blur' }]
      },
      submitBtnLoading: false, // loading
      getCodeLoading: false,
      time: 3, // 
      codeText: '',
      getCodeFlag: true //  true  false 
    }
  },
  computed: {},
  watch: {},
  created () {},
  mounted () {},
  methods: {
    //   
    onSubmit () {
      // loading
      this.submitBtnLoading = true
      // 
      this.$refs.loginFormRef.validate((vaild) => {
        // loading  loading
        if (!vaild) {
          this.submitBtnLoading = false
          return this.$message({
            message:
              '' +
              (this.isLogin ? '' : '') +
              '',
            type: 'warning'
          })
        }
        const user = this.loginForm
        //   
        if (this.isLogin) {
          this.onLogin(user)
        } else {
          this.onRegister(user)
        }
      })
    },
    // 
    async onLogin (user) {
      //  account  
      const loginData = {
        account: user.email,
        password: user.password
      }
      try {
        const { data: res } = await login(loginData)
        if (res.code !== 0) {
          this.submitBtnLoading = false
          return this.$message.error(res.msg)
        }
        this.$store.commit('setUser', res.user)
        this.$store.commit('setToken', res.token)
        this.$emit('close')
        this.$message({ message: '', type: 'success' })
      } catch (error) {
        this.$message.error('')
        this.submitBtnLoading = false
      }
      this.submitBtnLoading = false
    },
    // 
    async onRegister (user) {
      try {
        const { data: res } = await register(user)
        console.log(res)
        if (res.code !== 0) {
          this.submitBtnLoading = false
          return this.$message.error(res.msg)
        }
        this.$message({ message: '', type: 'success' })
        this.$emit('update-is_Login', !this.isLogin)
      } catch (error) {
        this.$message.error('')
        this.submitBtnLoading = false
      }
      this.submitBtnLoading = false
    }
  }
}
</script>
<style scoped lang="less">
@maincolor: #3fc2a6;
/deep/.el-form-item__content {
  margin: 0 !important;
}
.submitBtn {
  width: 100%;
  background-color: #33aba0;
  color: #fff;
}
.submitBtn:hover {
  background-color: @maincolor;
}
</style>
